import React, { Component } from "react";
import PropTypes from "prop-types";
import "./Card.scss";

export default class Card extends Component {
  static propTypes = {
    name: PropTypes.string
  };

  state = {
    count: 0
  };

  handleIncrement = () => {
    let count = this.state.count + 1;

    this.setState({
      count
    });
  };

  handleDecrement = () => {
    if (this.state.count > 0) {
      let count = this.state.count - 1;

      this.setState({
        count
      });
    }
  };

  render() {
    return (
      <div className="flex-column">
        <div className="name">{this.props.name}</div>
        <div className="name">{this.state.count}</div>
        <div className="image">
          <img src={this.props.img} alt="Gambar" />
        </div>
        <div className="btn-group">
          <button className="btn up" onClick={this.handleIncrement}>
            +1
          </button>
          <button className="btn down" onClick={this.handleDecrement}>
            -1
          </button>
        </div>
      </div>
    );
  }
}
