import React, { Fragment } from "react";
import Card from "../Cards/Card";
import smft1 from "../../asset/smft1.png";
import smft2 from "../../asset/smft2.png";
import "./Smft.scss";

const Smft = () => {
  return (
    <Fragment>
      <div className="heading-smft">SMFT</div>
      <div className="flex-container">
        <div className="row">
          <Card name="Wayan Nopra Artame" img={smft1} />
        </div>
        <div className="row">
          <Card name="I Kadek Restu Ariatmaja" img={smft2} />
        </div>
      </div>
    </Fragment>
  );
};

export default Smft;
