import React from "react";
import Card from "./Cards/Card";
import "./Layout.scss";
import Smft from "./SMFT/Smft";
import Bpmft from "./BPMFT/Bpmft";
import reject from "../asset/reject.png";

const Layout = () => {
  return (
    <div className="container">
      <div className="header">MUSMA TEKNIK 2019</div>
      <div className="main">
        {/* SMFT */}
        <div className="smft grid">
          <Smft />
        </div>
        {/* BPMFT */}
        <div className="bpmft grid">
          <Bpmft />
        </div>
      </div>
      <div className="invalid-vote">
        <Card name="Suara Tidak Sah" img={reject} />
      </div>
      <div className="footer">
        <p>Code by Kambing</p>
      </div>
    </div>
  );
};

export default Layout;
