import React, { Fragment } from "react";
import Card from "../Cards/Card";
import bpm1 from "../../asset/bpm1.png";
import bpm2 from "../../asset/bpm2.png";
import "./Bpmft.scss";

const Bpmft = () => {
  return (
    <Fragment>
      <div className="heading-bpmft">BPMFT</div>
      <div className="flex-container">
        <div className="row">
          <Card name="I Wayan Gita Riesta" img={bpm1} />
        </div>
        <div className="row">
          <Card name="I Komang Gede Dharma Yasa" img={bpm2} />
        </div>
      </div>
    </Fragment>
  );
};

export default Bpmft;
